html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}
/* reset */
#root {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(20, 1fr);
  grid-template-rows: repeat(20, 1fr);
  font-family: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Apple Color Emoji", Helvetica, Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol"';
}

#botaoAbrirModal {
  padding-right: 20px;
  align-self: center;
  grid-row: 1;
  grid-column: 1;
  height: 40px;
  width: max-content;
  background-color: none;
  border: none;
  &&:hover {
    background-color: rgba(228, 228, 228, 0.678);
  }
  &&::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    display: inline-block;
    width: 20px; /* Defina o tamanho conforme necessário */
    height: 20px;
    margin-right: 8px; /* Espaço entre o texto e o ícone */
    vertical-align: middle; /* Alinha o ícone ao texto */
  }
}

#hoje {
  grid-row: 2;
  grid-column: 7;
  font-size: 30px;
}

#qntTarefasAtivas {
  grid-row: 3;
  grid-column: 7;
}

#divDeTarefas {
  grid-row: 4;
  grid-column: 7/9;
}

#divDeProjetos {
  padding: 10px;
  display: flex;
  gap: 20px;
  flex-direction: column;
  grid-row: 2;
  grid-column: 1;
  font-size: 14px;
  width: max-content;
  && h3 {
    font-weight: 600;
    color: grey;
  }
  && h4 {
    &&::before {
      content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
      display: inline-block; /* Garante que o ícone seja exibido */
      width: 24px; /* Largura do SVG */
      height: 24px; /* Altura do SVG */
      margin-left: 8px; /* Espaço entre o texto e o ícone */
      vertical-align: middle; /* Alinha o ícone ao texto */
    }
  }
}

#divDeTarefasInativas {
  grid-row: 7;
  grid-column: 7/9;
}

.caixaModal {
  position: absolute;
  top: 15%;
  left: 33%;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  max-width: 90%;
  z-index: 1;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2), 0 6px 20px rgba(0, 0, 0, 0.19); /* Sombra suave */
}

.divModalProjetos {
  position: absolute;
  top: 26%;
  left: 34.3%;
  z-index: 2;
}

.divModalPrioridades {
  position: absolute;
  top: 24%;
  left: 42%;
  z-index: 2;
}

.tarefaProjeto {
  margin-left: 10px;
  color: gray;
}

#sidebar {
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-column: 1/4;
  grid-row: 1/-1;
  background-color: rgb(243, 243, 243);
}

.tarefa-item {
  && h3 {
    font-size: 18px;
    font-weight: 600;
    color: red;
  }
  && p:nth-of-type(1) {
    color: blue;
    font-style: italic;
  }
}
