import { getTarefas } from "../modules/tasks/taskManager";
import { parseISO, isBefore } from "date-fns";

function exibirTarefasAtivas() {
  const qntTarefasAtivas = document.querySelector("#qntTarefasAtivas");
  qntTarefasAtivas.innerHTML = `${getTarefas().length} tarefas`;
}

const verificarTarefasAtrasadas = (tarefas) => {
  const divTarefasAtrasadas = document.querySelector("#divDeTarefasInativas"); // Div para tarefas atrasadas
  const divTarefas = document.querySelector("#divDeTarefas"); // Div para tarefas gerais

  // Pega a data atual
  const hoje = new Date();

  // Percorre todas as tarefas
  tarefas.forEach((tarefa) => {
    const dataLimite = parseISO(tarefa.data); // Converte a string da data para um objeto Date

    // Verifica se a tarefa está atrasada
    if (isBefore(dataLimite, hoje)) {
      // Remove a tarefa da lista de tarefas ativas no DOM
      const tarefaElemento = document.querySelector(
        `.tarefa-item[data-id="${tarefa.id}"]`
      );
      if (tarefaElemento) {
        tarefaElemento.remove(); // Remove a tarefa do DOM atual
      }

      // Move a tarefa para o div de atrasadas
      const novaDiv = document.createElement("div");
      novaDiv.classList.add("tarefa-item");
      novaDiv.setAttribute("data-id", tarefa.id);
      novaDiv.innerHTML = `
        <h3>${tarefa.titulo}</h3>
        <p>${tarefa.descricao}</p>
        <p>Data Limite: ${tarefa.data}</p>
        <p>Projeto: ${tarefa.projetos}</p>
      `;
      divTarefasAtrasadas.appendChild(novaDiv);
    }
  });
};

export { exibirTarefasAtivas, verificarTarefasAtrasadas };
