// modules/tasks/taskManager.js

import { salvarTarefas, carregarTarefas } from "../../data/storage";

let tarefas = carregarTarefas(); // Carrega as tarefas ao iniciar o programa

export function getTarefas() {
  return tarefas;
}

export function adicionarTarefa({
  titulo,
  descricao,
  prioridade,
  data,
  projetos,
}) {
  const novaTarefa = {
    id: Date.now(),
    titulo,
    descricao,
    prioridade,
    data,
    projetos,
    status: "ativa", // Adiciona status padrão
  };

  tarefas.push(novaTarefa);
  salvarTarefas(tarefas); // Salva no localStorage
  return novaTarefa;
}

export function removerTarefa(id) {
  const indice = tarefas.findIndex((tarefa) => tarefa.id === id);
  if (indice !== -1) {
    tarefas.splice(indice, 1); // Remove do array
    salvarTarefas(tarefas); // Atualiza o localStorage
  }
}
