// modules/storage/storage.js

// Função para salvar tarefas no localStorage
export function salvarTarefas(tarefas) {
  localStorage.setItem("tarefas", JSON.stringify(tarefas));
}

// Função para carregar tarefas do localStorage
export function carregarTarefas() {
  const tarefas = localStorage.getItem("tarefas");
  return tarefas ? JSON.parse(tarefas) : []; // Retorna um array vazio se não houver tarefas
}

// Função para limpar todas as tarefas do localStorage
export function limparTarefas() {
  localStorage.removeItem("tarefas");
}
